export default{
  data () {
    return {
      currentList: null
    }
  },
  props: ['list'],
  computed: {
    activeList () {
      if (this.currentList) {
        return this.currentList
      } else {
        return this.list[0]
      }
    }
  },
  methods: {
    selectList (o) {
      this.currentList = o
      this.$emit('selectList', o)
    }
  }
}



